html, body, p, div, h1, h2, h3, h4, h5, h6, img, pre, form, fieldset, section {
  margin: 0;
  padding: 0;
}

body {
  font-family: sans-serif;
}

.ske-panorama {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: 1fr;
  height: 87vh;
}

.ske-cascade {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(2, 1fr);
  height: 87vh;
}

.ske-kolonne {
  width: 100%;
  height: 100%;
  padding: 0 1px;
  margin: 0 1px;
}

.ske-segment {
  background: #f4f5ff;
  border: #dcdded 1px solid;
  border-radius: 3px;
  margin: 4px 0;
  padding: 2px;
  overflow: auto;
}

  /* ske-oppgave-*, copied from JIRA */
.ske-oppgave {
  background: #fff;
  border: 1px solid #ccc;
  color: #333;
  display: block;
  font-size: 14px;
  line-height: 1.42857143;
  margin-top: -1px;
  position: relative;

  text-align: left;
}

.ske-oppgave:hover {
  background: #f5f5f5;
}

.ske-oppgave-markert {
  background: beige;
}

.ske-oppgave-innhold {
  min-height: 66px;
  padding: 10px 10px 10px 38px;
}

.ske-oppgave-felter {
  padding-right: 37px;
}

.ske-oppgave-type {
  height: 16px;
  width: 16px;
  overflow: hidden;

  left: 13px;
  position: absolute;
  top: 10px;
}

.ske-oppgave-key {
  display: inline-block;
  line-height: 1;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
}

.ske-oppgave-oppsumering-indre {
  display: block;
  line-height: 1.1;
  max-height: 3.3em;
  overflow: hidden;
  word-wrap: break-word;
}

.ske-oppgave-avatar {
  display: block;
  position: absolute;
  right: 10px;
  top: 10px;
}

.ske-oppgave-avatar-img {
  border-radius: 3px;
  font-size: 24px;
  line-height: 32px;
  width: 32px;
  height: 32px;
  color: #fff;
  display: inline-block;
  text-align: center;
  text-transform: uppercase;
  vertical-align: middle;

  border: 0;
}

.ske-oppgave-flagg, .ske-oppgave-type, .ske-oppgave-prioritet, .ske-oppgave-status {
  width: 16px;
  height: 16px;
  overflow: hidden;
}

.ske-oppgave-flagg-prioritet {
  left: 13px;
  position: absolute;
  top: 31px;
}

.ske-oppgave-flagg-status {
  left: 13px;
  position: absolute;
  top: 52px;
}

.ske-oppgave-filter {
  text-align: left;
  padding: 5px;
}

.ske-utgivelse-filter {
  height: 40px;
  margin: 4px;
  background: #f4f5ff;
  border: #dcdded 1px solid;
  border-radius: 3px;
}

.ske-oppgaver {
  width: 100%;
}


